.header{
  display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
  
  }
 .header_logo{
     height: 50px;
     object-fit: contain;
     margin-left: 20px;
     cursor: pointer;
 }
  .header_input{
    display: flex;
    align-items:center;
    width: 40%;
    border: 1px solid black;

      
  
  }
  .header-input-Button{
      width:50px !important;
      background-color: #fafafa;
      border-left:1px solid lightgray ;
      cursor: pointer;

  }
  .header_icons{
    display: flex;
    align-items: center;
    justify-content: space-between;
  
  }
  .header_input > input{
      flex: 1;
      border: none;
  }
  .header_icon{
      margin-right: 9px;

  }
  .my_pic{
    cursor: pointer;
  }
  .header_left{
    cursor: pointer;
  }